import React, {useState} from "react"
import "../styles/skills.css"
import {ReactComponent as Previous} from "../images/baseline-keyboard_arrow_left-64px.svg"
import {ReactComponent as Next} from "../images/baseline-keyboard_arrow_right-64px.svg"
import {ReactComponent as Unselected} from "../images/tab-unselected.svg"
import {ReactComponent as Selected} from "../images/tab-selected.svg"


const Skills = (props) => {
  //can add new skills and this should scale without problems.
  const [currentItemIndex, setCurrentItemIndex] = useState(0)

  const showPrevItem = (event) => {
    event.preventDefault()
    currentItemIndex === 0?
      setCurrentItemIndex(props.content.skills.length -1)
      :
      setCurrentItemIndex(currentItemIndex-1)
  }
  const showNextItem = (event) => {
    event.preventDefault()
    currentItemIndex === props.content.skills.length -1?
      setCurrentItemIndex(0)
      :
      setCurrentItemIndex(currentItemIndex+1)
  }

  return(
    <div className="skillsContainer">
      <div className="skillsTitle">
        <p className="skillsTitleText">Taidot</p>
      </div>
      <div className="skillsContainerInner">
        <div className="skillsArrow" onClick={showPrevItem}>
          <Previous className="skillsIcon"/>
        </div>
        <div className="skillsContent">
          <p className="skillsTextContent">{props.content.skills[currentItemIndex]}</p>

        </div>
        <div className="skillsArrow">
          <Next className="skillsIcon" onClick={showNextItem}/>
        </div>

      </div>
      <div className="tabsBubblesContainer">
        <div className="tabsBubblesContainerInner">
          {props.content.skills.map((item,i) => (
            i===currentItemIndex?
              <Selected key={i} className="selectedIcon"/>
              :
              <Unselected key={i} className="unselectedIcon" onClick={() => setCurrentItemIndex(i)}/>
          ))}
        </div>
      </div>
    </div>
  )
}

export default Skills