import React from "react"
import "../styles/cvInfo.css"





const CvInfo = (props) => {
  console.log(props.content)
  return(
    <div className="infoContainer">
      <div className="infoContainerInner">
        <p className="infoHeader">{props.content.name}</p>
        <p className="contactInfoText">
          <span>{props.content.address} </span>
          <span>{props.content.zipAndCity}</span>
        </p>
        <p className="contactInfoText">{props.content.email}</p>
        <p className="contactInfoText">{props.content.phone}</p>


      </div>

    </div>
  )
}
export default CvInfo