import React, {useState} from "react"
import {connect} from "react-redux"
import {createComment} from "../reducers/commentReducer"
import {notify} from "../reducers/notificationReducer"

import "../styles/commentSectionAlt.css"




const CommentSectionAlt = (props) => {
  const [currentComment, setCurrentComment] = useState("")
  const comments = props.comments
  const commentField = document.getElementsByClassName("inputAlt")

  const cancelComment = (event) => {
    event.preventDefault()
    setCurrentComment("")
    commentField.content.value = ""
  }

  const getSortedComments = () => {
    const commentsToShow = [...comments]
    commentsToShow.sort(function(a,b){
      return new Date(b.date) - new Date(a.date)
    })
    return commentsToShow

  }

  const commentChangeHandler = (event) => {
    event.preventDefault()
    setCurrentComment(event.target.value)
    event.target.setAttribute("rows", parseInt((commentField.content.scrollHeight) / 18))

  }

  const addComment = async (event) => {
    event.preventDefault()
    console.log("submit")
    const content = event.target.content.value
    event.target.content.value = ""
    if(content.length <3){
      props.notify("Kommentin minimipituus on kolme merkkiÃ¤.", true, 5)
      setCurrentComment("")
      return
    }
    props.createComment(content)
    setCurrentComment("")

  }

  const howLongAgo = (dateJson) => {
    //takes database dateJson string and returns how long ago that was.
    //less than hour in minutes
    //less than day in hours
    //less than month in days
    //less than year in months
    //otherwise years

    const deltaTime = new Date() - new Date(dateJson)
    const unitsAndThresholds = [["vuotta", 31536000000],["kuukautta", 2592000000],["pÃ¤ivÃ¤Ã¤", 86400000],["tuntia", 3600000],["minuuttia", 60000], ["sekunttia", 1000]]
    const unitsSingular = ["vuosi", "kuukausi", "pÃ¤ivÃ¤", "tunti", "minuutti", "sekuntti"]

    for(let i=0; i<unitsAndThresholds.length;i++){
      if(Math.floor(deltaTime/unitsAndThresholds[i][1]) === 1){
        return `1 ${unitsSingular[i]} sitten.`
      }else if (deltaTime/unitsAndThresholds[i][1] > 1){
        return `${Math.floor(deltaTime/unitsAndThresholds[i][1])} ${unitsAndThresholds[i][0]} sitten.`
      }
    }
    return "Just Now."
  }
  return(
    <div className="CSAltContainer">
      <div className="CSAltContainerInner">
        <p className="headerTextLeft">{props.comments.length} Kommenttia</p>
        <div className="divider"/>
        <form onSubmit={addComment}>
          <div className="commentContainer inputContainerAlt">
            <textarea onChange={commentChangeHandler} rows="1" maxLength="256" className="inputAlt" type="text" name="content" placeholder="Uusi Kommentti."/>
            <div className="inputFocusLineComment inputFocusLine"></div>
            {(currentComment.length === 0)?
              <div></div>
              :
              <div className="commentButtonsContainer">
                <button name="submit" className="positiveButton rippleButton">Tallenna</button>
                <button name="cancel" onClick={cancelComment} className="negativeButton rippleButton">Peruuta</button>

              </div>
            }
          </div>


        </form>
        <ul className="commentSectionList">
          {(comments.length === 0)?

            <p className="commentText"> Ei yhtÃ¤Ã¤n kommenttia vielÃ¤. Â¯\_(ã)_/Â¯</p>


            :
            getSortedComments().map(comment  =>
              <li key={comment.id} className="commentListItem">
                <span className="commentAuthor">{comment.user.name? comment.user.name : "You"} @ </span>
                <span className="commentDate">{howLongAgo(comment.date)}</span>
                <p className="commentText">{comment.content}</p>

              </li>
            )
          }

        </ul>

      </div>

    </div>
  )
}

const mapStateToProps = (state) => {
  return {
    comments: state.comments
  }
}

const mapDispatchToProps = {
  createComment,
  notify
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CommentSectionAlt)