import React from "react"
import "../styles/education.css"

const Education = (props) => {

  const setBackgroundOnHover = (index) => {

    switch(index){
    case 0:
      //document.getElementsByClassName("educationContainerAlt")[0].setAttribute("style",`background-image:${image};`)
      //document.getElementsByClassName("educationContainerAlt")[0].style.setProperty("background",image)
      document.getElementsByClassName("imgBg1")[0].style.visibility = "visible"
      break
    case 1:
      document.getElementsByClassName("imgBg2")[0].style.visibility = "visible"
      break
    case -1:
      document.getElementsByClassName("imgBg1")[0].style.visibility = "hidden"
      document.getElementsByClassName("imgBg2")[0].style.visibility = "hidden"
      break

    }

  }

  return(
    <div className="educationContainerAlt">
      <div className="imgBg1"/>
      <div className="imgBg2"/>
      <div className="educationTitle">
        <p className="educationTitleText">Koulutus</p>
      </div>

      <div className="educationContainerInner">
        {props.content.education.slice(0).reverse().map((element,i) => (

          <li key={i} className="educationListItem" onMouseEnter={() => setBackgroundOnHover(i)} onMouseLeave={() => setBackgroundOnHover(-1)}>
            <p className="educationTextContent">{element.name}</p>
            <p className="educationTextContent">
              {element.degree} {element.graduation}
            </p>
            {(element.link !== undefined && element.link !== "")?
              <p className="educationTextContent"> KandidaatintyÃ¶: <a className="educationTextContent" href={element.link}>Linkki</a></p>
              :
              <div></div>
            }
          </li>

        ))
        }


      </div>
      
    </div>
  )

}

export default Education