import React from "react"
import MenuAlt from "./MenuAlt"
import Footer from "../components/Footer"
import ContentAlt from "./ContentAlt"



const FlashyCV = (props) => {
  return(
    <div className="appContainer">
      <MenuAlt history={props.history}/>

      <ContentAlt/>

      <Footer/>

    </div>
  )
}
export default FlashyCV