import React,{useState, useEffect} from "react"
import {connect} from "react-redux"
import {logout} from "../reducers/loginReducer"
import {notify} from "../reducers/notificationReducer"
import "../styles/menuAlt.css"
import "../styles/texts.css"
import "../styles/buttons.css"

export const Menu = (props) => {
  const [pos, setPos] = useState(window.pageYOffset)

  useEffect(() => {
    const handleScroll = () => {
      let temp = window.pageYOffset
      if(pos < temp){
        document.getElementsByClassName("altMenuContainer")[0].style.setProperty("top","-100px")
        setPos(temp)

      }else{
        document.getElementsByClassName("altMenuContainer")[0].style.setProperty("top","0")
        setPos(temp)
      }

    }
    window.addEventListener("scroll", handleScroll)
    return(() => {
      window.removeEventListener("scroll", handleScroll)
    })
  })

  const navigate = (event) => {
    event.preventDefault()
    console.log(props.history)
    if(props.history.location.pathname==="/"){
      props.history.push("/v1")
    }else{
      props.history.push("/")
    }
  }
  const logout = (event) => {
    event.preventDefault()
    console.log("Logging out")
    props.logout(props.notify)
  }
  return (
    <div className="altMenuContainer">
      <div className="altMenuGrid">
        <div className="altMenuLogo">
          <p className="logoText">Niklas ImpiÃ¶ Cv App</p>
        </div>
        <ul className="altMenuButtonList">
          <li className="menuListItem" onClick={logout}>
            <p className="menuItemText">Kirjaudu Ulos</p>
          </li>
          <li name="" className="menuListItem" onClick={navigate}>

            <p name="" className="menuItemText">{props.history.location.pathname === "/v1"? "2. Versio " : "1. Versio"}</p>
          </li>

        </ul>

      </div>
    </div>
  )
}

export default connect(
  null,
  {notify, logout}
)(Menu)