import React from "react"
import "../styles/profile.css"

import {ReactComponent as Icon0} from "../images/baseline-person-24px.svg"
import {ReactComponent as Icon1} from "../images/baseline-home-24px.svg"
import {ReactComponent as Icon2} from "../images/baseline-assignment_turned_in-24px.svg"
import {ReactComponent as Icon3} from "../images/baseline-group-24px.svg"
import {ReactComponent as Icon4} from "../images/baseline-code-24px.svg"
import {ReactComponent as Icon5} from "../images/baseline-memory-24px.svg"




const Profile = (props) => {
  console.log(props.content)
  const iconList = [<Icon0 key="0" className="icon"/>,
    <Icon1 key="1" className="icon"/>,
    <Icon2 key="2" className="icon"/>,
    <Icon3 key="3" className="icon"/>,
    <Icon4 key="4" className="icon"/>,
    <Icon5 key="5" className="icon"/>]

  return(

    <div className="profileContainer">
      <div className="profileTitle">
        <p className="profileTitleText">Profiili</p>
      </div>
      <div className="profileContainerInner">
        <div className="profileListContainer">
          {props.content.profile.map((element,i) => (
            <li key={i} className="contentList">
              <div className="profileContentContainer">
                {iconList[i]}
                <p className="textContentProfile"> {element}</p>
              </div>

            </li>
          ))}
        </div>

      </div>

    </div>
  )
}
export default Profile