import React from "react"
import "../styles/jobs.css"


const JobHistory = (props) => {
  return(
    <div className="jobContainer">
      <div className="jobsTitle">
        <p className="jobsTitleText">TyÃ¶kokemus</p>
      </div>
      <div className="jobsContainerInner">
        {
          props.content.jobHistory.map((element,i) => (
            <div key={i} className="jobsListItem">
              <li key={i} className="jobsContentList">
                <p className="jobsTextContent"> {element.jobTitle}</p>
                <p className="jobsTextContent">{element.employer}</p>
                <p className="jobsTextContent">{element.duration}</p>
              </li>
            </div>
          ))
        }
      </div>

    </div>
  )
}

export default JobHistory