import React from "react"
import {connect} from "react-redux"
import "../styles/contentAlt.css"
import CvInfo from "./CvInfo"
import Profile from "./Profile"
import Education from "./Education"
import JobHistory from "./JobHistory"
import Skills from "./Skills"
import CommentSectionAlt from "./CommentSectionAlt"




const ContentAlt = (props) => {
  return(
    <div>
      {props.content.length !== 0?
        <div className="contentAltGrid">
          <CvInfo content={props.content}/>
          <Profile content={props.content}/>
          <Education content={props.content}/>
          <JobHistory content={props.content}/>
          <Skills content={props.content}/>
          <CommentSectionAlt content={props.content}/>

        </div>
        :
        <div/>
      }
    </div>
  )
}
const mapStateToProps = (state) => {
  return {
    content: state.content
  }
}

export default connect(
  mapStateToProps
)(ContentAlt)